// Filter styles.
// -----------------------------------------------------------------------------
@import "../../bootstrap/less/variables.less";  // Bootstrap variables.
@import "../../bootstrap/less/mixins.less";     // Bootstrap mixins.
@import "../variables.less";                    // oc_theme variables.
@import "../mixins.less";                       // oc_theme mixins.
// -----------------------------------------------------------------------------

// Mimic .panel-default styling.
.filter-wrapper {
  background-color: @panel-bg;
  border: 1px solid @panel-default-border;
  border-top: 0;
  border-radius: 0 0 @panel-border-radius @panel-border-radius;
  box-shadow: 0 1px 1px rgba(0,0,0,.05);
  margin-bottom: 0;
  padding: 10px;
  height: 51px;
  &:extend(.clearfix all);
}
.filter-help {
  float: right;
  line-height: 1;
  margin: .5em 0 0;
}

// Full list page.
.nav.nav-tabs.filter-formats {
  margin-bottom: 15px;
}
